/**
 * Text input
 */
.ult-form-password-wrapper,
*:not(.ult-form-password-wrapper) > .ult-form__input,
.ult-form__container *:not(.ult-form-password-wrapper) > input[type="tel"],
.ult-form__container *:not(.ult-form-password-wrapper) > input[type="text"],
.ult-form__container *:not(.ult-form-password-wrapper) > input[type="password"] {
	padding: var(--ult-form-field-padding, 8px 10px);
	color: var(--ult-form-field-color, #404040);
	background: var(--ult-form-field-background, #fff);
	box-shadow: var(--ult-form-field-box-shadow, 0 2px 5px 0 rgba(0,0,0,.1));
	border: 1px solid var(--ult-form-field-border-color, #c1c5c8);
	border-radius: var(--ult-form-field-border-radius, 8px);

	width: 100%;
	font-size: inherit;
	font-family: inherit;
	outline: none;
	display: block;
	margin-top: 8px;
	line-height: 1.3;
	transition: border 150ms ease-in-out, box-shadow 150ms ease-in-out;

	&:hover {
		border-color: var(--ult-form-field-hover-border-color, #b1b5b7);
	}

	&.ult-form-password-wrapper--focus,
	&:focus {
		border-color: var(--ult-form-field-focus-border-color, $primary_color);
		box-shadow: var(--ult-form-field-focus-box-shadow, 0 0 0 0.2rem rgba( $primary_color, .2 ));
	}
}

.ult-form-password-wrapper {
	display: flex;
	align-items: center;
	gap: 5px;

	input {
		flex: 1 1 auto;

		font-size: inherit !important;
		font-family: inherit !important;
		border: 0 !important;
		box-shadow: none !important;
		padding: 0 !important;
		margin: 0 !important;
		outline: 0 !important;
		
		/* Hide browser-generated password toggle icons to prevent duplicates */
		&::-ms-reveal,
		&::-ms-clear {
			display: none;
		}

		/* Hide WebKit password toggle icons */
		&::-webkit-credentials-auto-fill-button,
		&::-webkit-credentials-auto-fill-button:hover,
		&::-webkit-credentials-auto-fill-button:active {
			display: none !important;
			visibility: hidden !important;
			pointer-events: none !important;
		}

		/* Additional WebKit password field styling to prevent duplicate icons */
		&::-webkit-textfield-decoration-container {
			display: none !important;
		}

		/* For Firefox password field */
		&::-moz-textfield-decoration-container {
			display: none !important;
		}

		/* Ensure clean appearance without browser defaults */
		-webkit-appearance: none;
		-moz-appearance: none;
		appearance: none;
	}

	&-eye-toggle {
		width: 20px;
		height: 20px;

		display: flex;
		align-items: center;
		justify-content: center;

		color: #7f7f7f;

		cursor: pointer;

		flex: 0 0 auto;
	}
}